.clearfix::after {
  visibility: hidden;
  display: block;
  font-size: 0;
  content: '';
  clear: both;
  height: 0;
}
.wrapper {
  background: url("../images/active-house.jpg") center;
}
* {
  margin: 0;
  box-sizing: border-box;
  font-family: 'Roboto', 'Open Sans';
  outline: 0;
}
html,
body,
.wrapper {
  height: 100%;
}
body {
  background: #14223e;
}
.wrapper {
  padding: 3%;
  position: relative;
  background-size: cover;
  color: #fff;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 100;
}
.header {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  font-size: 0;
  text-align: center;
}
.header::before {
  position: absolute;
  content: '';
  width: 0;
  display: block;
  height: 2px;
  background: #fff;
}
.header-anim::before {
  animation: head-line 2s linear 0.8s forwards;
}
.header-menu {
  display: inline-block;
  width: 60%;
  text-align: center;
  list-style: none;
  -webkit-padding-start: 0;
  -moz-padding-start: 0;
  -ms-padding-start: 0;
  padding-start: 0;
  font-size: 0;
}
.menu-bg {
  display: inline-block;
  background-image: radial-gradient(#fff, transparent);
  box-shadow: 0 0 100px rgba(255,255,255,0.6), 0 0 100px rgba(255,255,255,0.6), 0 0 100px rgba(255,255,255,0.6), 0 0 100px rgba(255,255,255,0.6), 0 0 100px rgba(255,255,255,0.6), 0 0 100px rgba(255,255,255,0.6), 0 0 100px rgba(255,255,255,0.6), 0 0 100px rgba(255,255,255,0.6), 0 0 100px rgba(255,255,255,0.6);
}
.menu-item {
  width: 15%;
  display: inline-block;
  vertical-align: top;
  color: #fff;
  font: 300 22px/24px Roboto;
  border: 1px solid #f00;
}
.menu-item:nth-child(1) {
  width: 22%;
}
.menu-item:nth-child(2) {
  width: 18%;
}
.menu-item::before {
  content: '';
  display: block;
  height: 46px;
  width: 1px;
  background: #fff;
  margin: -46px auto 0;
  transition: 0.4s;
}
.menu-item:hover {
  cursor: pointer;
  color: #00976f;
}
.menu-item:hover > ul {
  transform: scale(1.3);
  font-weight: 400;
}
.menu-item:hover::before {
  height: 87px;
}
.menu-item > ul {
  padding: 14px 0;
  opacity: 0;
  -webkit-padding-start: 0;
  -moz-padding-start: 0;
  -ms-padding-start: 0;
  padding-start: 0;
  transition: 0.4s;
  transform-origin: center;
}
.menu-item.visible > ul {
  opacity: 1;
}
:nth-child(1)::before {
  animation: anim-before 0.4s linear 1.4s forwards;
}
:nth-child(2)::before {
  animation: anim-before 0.4s linear 1.6s forwards;
}
:nth-child(3)::before {
  animation: anim-before 0.4s linear 1.8s forwards;
}
:nth-child(4)::before {
  animation: anim-before 0.4s linear 2s forwards;
}
:nth-child(5)::before {
  animation: anim-before 0.4s linear 2.2s forwards;
}
:nth-child(6)::before {
  animation: anim-before 0.4s linear 2.4s forwards;
}
@-moz-keyframes head-line {
  100% {
    width: 100%;
  }
}
@-webkit-keyframes head-line {
  100% {
    width: 100%;
  }
}
@-o-keyframes head-line {
  100% {
    width: 100%;
  }
}
@keyframes head-line {
  100% {
    width: 100%;
  }
}
@-moz-keyframes anim-before {
  100% {
    margin-top: 0;
  }
}
@-webkit-keyframes anim-before {
  100% {
    margin-top: 0;
  }
}
@-o-keyframes anim-before {
  100% {
    margin-top: 0;
  }
}
@keyframes anim-before {
  100% {
    margin-top: 0;
  }
}
.left-side {
