}
.menu-apart-white:hover .menu-apart-item:nth-child(3)::after {
  animation: point-1 1.8s linear 0s infinite;
}
.menu-apart-items {
  display: inline-block;
  vertical-align: middle;
}
.menu-apart-item {
  position: relative;
  width: 23px;
  height: 2px;
  margin: 6px 0;
  background: #008000;
  transition: 0.4s;
}
.menu-apart-item::after {
  transform-origin: center;
  box-sizing: border-box;
  content: '';
  position: absolute;
  top: -3px;
  width: 8px;
  height: 8px;
  border-radius: 50%;
  border: 2px solid #008000;
  background: #fff;
}
.menu-apart-item:nth-child(1)::after {
  right: 20%;
}
.menu-apart-item:nth-child(2)::after {
  right: 40%;
}
.menu-apart-item:nth-child(3)::after {
  right: 20%;
}
