  padding: 12px 0 12px 15px;
  border: 2px solid #fff;
  background: rgba(255,255,255,0.6);
}
.all-actions > * {
  display: inline-block;
  vertical-align: middle;
  transition: 0.4s;
  backface-visibility: hidden;
}
.all-actions > svg {
  margin-left: 16px;
}
.all-actions:hover > * {
  transform: scale(1.2);
}
